.container {
  max-width: calc(1440px - 168px * 2 + 48px * 2);
  margin-right: auto;
  margin-left: auto;
  padding-left: 48px;
  padding-right: 48px;


  @media (max-width: $screen-md) {
    padding-left: 32px;
    padding-right: 32px;
  }

  @media (max-width: $screen-sm) {
    padding-left: 28px;
    padding-right: 28px;
  }

  @media (max-width: $screen-xxs) {
    padding-left: 16px;
    padding-right: 16px;
  }
}

